export default {
	data() {
		return {
			system:this.systemInfo(),
			login:false,
			user:{},
			count:{},
			myvideolist:[],
			browserlist:[],
			likelist:[]
		}
	},
	onLoad(option) {
		this.init();
		let user = this.getUserInfo();
		if(!user){
			uni.$once('logon',(data) => {
				this.init();
			});
		}
	},
	methods:{
		init(){
			try{
				let user = this.getUserInfo();
				if(user){
					
					this.user = user;
					this.login = true;
					
					this.postJson({
						path:'/user_count_query.jsp',
						data:{uid:user.uid},
						success:(res) => {
							if("ok" == res.data.status){
								this.count = res.data.count;
							}
						}
					});
					
					
					this.postJson({
						path:'/user_video_list.jsp',
						data:{
							type:0
						},
						success:(res)=>{
							if("ok" == res.data.status){
								this.myvideolist = res.data.data;
								for(let x in this.myvideolist){
									this.myvideolist[x].logtimelabel = new Date(this.myvideolist[x].log_time).toLocaleDateString();
								}
							}
						}
					});
					
					this.postJson({
						path:'/user_video_list.jsp',
						data:{
							type:1
						},
						success:(res)=>{
							if("ok" == res.data.status){
								this.browserlist = res.data.data;
								for(let x in this.browserlist){
									this.browserlist[x].logtimelabel = new Date(this.browserlist[x].log_time).toLocaleDateString();
								}
							}
						}
					});
					
					this.postJson({
						path:'/user_video_list.jsp',
						data:{
							type:2
						},
						success:(res)=>{
							if("ok" == res.data.status){
								this.likelist = res.data.data;
								for(let x in this.likelist){
									this.likelist[x].logtimelabel = new Date(this.likelist[x].log_time).toLocaleDateString();
								}
							}
						}
					});
				}else{
					this.login = false;
					this.user = {};
				}
				
			}catch(e){
				console.error(e);
			}
		},
		tologin:function(){
			uni.navigateTo({
				url:"/pages/login/login?page=/pages/my/my"
			})
		},
		tosetting:function(){
			uni.navigateTo({
				url:"/pages/setting/setting"
			});
		},
		tovideo:function(e){
			let type = e.currentTarget.dataset.type;
			let index = e.currentTarget.dataset.index;
			uni.navigateTo({
				url:'/pages/video/video?type='+type+'&index='+index
			});
		},
		updateHeadImage(){
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从相册选择
				success: (res) => {
					console.log(JSON.stringify(res.tempFilePaths));
					
					uni.showLoading({
						title:"加载中"
					});
					this.loading = true;
					uni.uploadFile({
						url: 'https://www.dpsoft.top/upload.jsp', 
						filePath: res.tempFilePaths[0],
						name: 'file',
						
						success: (uploadFileRes) => {
							console.log(uploadFileRes.data);
							let data = JSON.parse(uploadFileRes.data);
							if("ok" == data.status){
								let webpathSuffix = data.data;
								let url = "http://www.dpsoft.top/"+webpathSuffix;
								this.user.head_img = url;
								let data = {
									headImg: url
								};
								this.postJson({
									path:'/setting_do.jsp',
									data:data,
									success:(res) => {
										if("ok" == res.data.status){
											this.setUserInfo(this.user);
										}
									}
								})
								
							}
							
						},
						complete: ()=> {
							uni.hideLoading();
						}
					});
					
				}
			});
		}
		
	}
}

